.info-wrapper {
  text-align: center;
}
.info-wrapper div:nth-of-type(odd) {
  background-color: rgba(128, 128, 128, 0.205);
}
.data {
  display: flex;
  justify-content: space-between;
  padding: 0 3% 0 3%;
}
img {
  width: 200px;
  height: 120px;
  border: 1px solid rgb(184, 184, 184);
}
.no-data {
  text-align: center;
}
.svg-wrapper {
  display: block;
  margin: auto;
}
.svg-container {
  margin-top: 10%;
}
.nodata-container {
  margin: auto;
  text-align: center;
}
@media only screen and (max-width: 1000px) {
  img {
    width: 100px;
    height: 60px;
    border: 1px solid rgb(184, 184, 184);
  }
  .data > h4 {
    font-size: small;
  }
}
